        height: 500px;
        margin-left: 60px;
        padding: 20px;
        background: $white-bg-color;

    }

    &__left {
        display: flex;
    }

    &__moves {
        text-align: right;
        font-size: 20px;
        color: $main-bg-color;
        font-size: 12px;
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        margin-right: 60px;

        p {
            margin-top: 10px;
            text-align: left;
            font-size: 30px;
            color: #d3d2e4;
        }
    }

    &__gridItem {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        width: 500px;
        background: $white-bg-color;
        padding: 20px;

    }

    &__resetBoard {
        border: 0;
        font-size: 12px;
        font-weight: 700;
        letter-spacing: 1px;
        border: 1px solid $main-bg-color;
        background: $white-bg-color;
