        transition: all 0.3s ease;

        &:hover {
            border: 1px solid $main-bg-color;
            background: $main-bg-color;
            color: $white-bg-color;

        }
    }

    &__previewCopy {
        font-size: 12px;
        margin-bottom: 5%;
    }

    &__previewImg {
        width: 150px;
    }
}
</style>
